document.addEventListener('DOMContentLoaded', function () {
    const fileInput = document.getElementById('fileInput');
    const previewContainer = document.getElementById('previewContainer');
    let filesArray = [];

    // Обработчик выбора файлов
    fileInput.addEventListener('change', function (e) {
        const files = Array.from(e.target.files);
        const newFiles = files.map(file => {
            return {
                file: file,
                rotation: 0 // начальный угол поворота
            };
        });
        filesArray = [...filesArray, ...newFiles];
        renderPreviews();
    });

    // Рендер превью
    function renderPreviews() {
        previewContainer.innerHTML = '';

        filesArray.forEach((fileObj, index) => {
            const reader = new FileReader();

            reader.onload = function (e) {
                const previewItem = document.createElement('div');
                previewItem.className = 'preview-item';
                previewItem.draggable = true;
                previewItem.innerHTML = `
                    <img src="${e.target.result}" alt="Preview" style="transform: rotate(${fileObj.rotation}deg)">
                    <button class="remove-btn preview-item__remove" data-index="${index}">
                    <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
                                         <path d="M8.75 0.75L0.75 8.75M0.75 0.75L8.75 8.75" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                   </svg>
    </button>
                    <button class="rotate-btn preview-item__rotate" data-index="${index}">
                    <svg width="15" height="14" viewBox="0 0 15 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M13.4167 5.41667C13.4167 5.41667 12.08 3.59548 10.9941 2.50883C9.90821 1.42218 8.4076 0.75 6.75 0.75C3.43629 0.75 0.75 3.43629 0.75 6.75C0.75 10.0637 3.43629 12.75 6.75 12.75C9.4854 12.75 11.7933 10.9195 12.5155 8.41667M13.4167 5.41667V1.41667M13.4167 5.41667H9.41667" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                            </svg>
                    </button>
                    <div class="drag-handle">⋮⋮</div>
                `;

                // Drag and Drop события
                previewItem.addEventListener('dragstart', (e) => {
                    e.dataTransfer.setData('text/plain', index);
                    previewItem.classList.add('dragging');
                });

                previewItem.addEventListener('dragend', () => {
                    previewItem.classList.remove('dragging');
                });

                previewItem.addEventListener('dragover', (e) => {
                    e.preventDefault();
                });

                previewItem.addEventListener('drop', (e) => {
                    e.preventDefault();
                    const fromIndex = parseInt(e.dataTransfer.getData('text/plain'));
                    const toIndex = index;
                    moveImage(fromIndex, toIndex);
                });

                // Удаление изображения
                const removeBtn = previewItem.querySelector('.remove-btn');
                removeBtn.addEventListener('click', function () {
                    const index = parseInt(this.getAttribute('data-index'));
                    removeImage(index);
                });

                // Поворот изображения
                const rotateBtn = previewItem.querySelector('.rotate-btn');
                rotateBtn.addEventListener('click', function () {
                    const index = parseInt(this.getAttribute('data-index'));
                    rotateImage(index);
                });

                previewContainer.appendChild(previewItem);
            };

            reader.readAsDataURL(fileObj.file);
        });
    }

    // Удаление изображения
    function removeImage(index) {
        filesArray.splice(index, 1);
        renderPreviews();
    }

    // Перемещение изображения
    function moveImage(fromIndex, toIndex) {
        const [movedItem] = filesArray.splice(fromIndex, 1);
        filesArray.splice(toIndex, 0, movedItem);
        renderPreviews();
    }

    // Поворот изображения
    function rotateImage(index) {
        filesArray[index].rotation = (filesArray[index].rotation + 90) % 360;
        renderPreviews();
    }
});